.backButton{
  margin-top: 42px;  
  display: flex;
  align-items: center;
  gap: 8px;

  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  border-radius: 8px;
  padding: 5px;
  color: var(--color-additional7);
  cursor: pointer;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backButton:hover,.backButton:focus{
  color: var(--color-additional);
}

@media (min-width: 768px) and (max-width: 1279px) {  

  .backButton {
    margin-top: 40px;
  }
}

@media (min-width: 1280px) {
 

  .backButton {
    margin-top: 18px;
  }

}